.sidebar {
  flex: 0.35;
  display: flex;
  flex-direction: column;
}
.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.sidebar__headerRight {
  display: flex;
  align-items: center;
  min-width: 10vw;
  justify-content: space-between;
}

.sidebar__search {
  display: flex;

  align-items: center;
  background-color: #f6f6ff;
  height: 39px;
  padding: 10px;
}

.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
  justify-content: center;
}
.sidebar__searchContainer input {
  border: none;
  margin-left: 10px;
}
.sidebar__searchContainer .MuiSvgIcon-root {
  color: gray;
  padding: 10px;
}

.sidebar__chats {
  flex: 1;
  background-color: white;
  overflow-y: scroll;
}
